import React from 'react'
import { Container, Header, Item, Segment } from 'semantic-ui-react'

export default function StatutePage() {
    return (
        <Container text >
            <Segment.Group>
                <Segment>
                    <Header as={'h2'} content='Zasady gry' />
                </Segment>
                <Segment>
                Gra Bet in Bed polega na typowaniu wynikw meczy podczas turnieju FIFA World Cup 2022. 
                </Segment>
                <Segment>
                - Zwycizcy zabawy rozstrzyga tabela po caej grze, wygrywa gracz ktry zbierze najwiksz ilo punktw przez cay okres turnieju, a trzech najlepszych graczy otrzymuje nagrody.
                </Segment>
                <Segment>
                - Zawodnicy zgoszeni do rywalizacji typuj wynik kadego odbywanego meczu podczas mundialu, a za kady trafny typ dostaj punkty;
                </Segment>
                <Segment>
                - Kady wynik meczu musi by wysany przed rozpoczciem meczu;
                </Segment>
                <Segment>
                - Kocowy wynik w rozgrywce jest sum wszystkich punktw zdobytych cznie we wszystkich 64 meczach odbywanych na Mistrzostwach wiata + suma punktw bonusowych                
                </Segment>
            </Segment.Group>
            <Segment.Group>
                <Segment>
                    <Header as='h2' content='Przyznawanie punktw' />
                </Segment>
                <Segment>
                - Za obstawiony mecz gracz moe otrzyma 0 , 2 lub 5 punktw;
                </Segment>
                <Segment>
                - Na podstawie prawdziwego wyniku na boiskach mundialowych, oceniamy czy gracz prawidowo wytypowa zwycizc meczu. Jeli tak, otrzymuje za to 2pkt., bez wzgldu na to czy prawidowo odgad wynik spotkania:
                </Segment>
                <Segment>
                *przykad 1: Gracz postawi wynik A 1:0 B, lecz mecz zakoczy si wynikiem A 3:1 B  gracz otrzymuje 2 punkty, gdy przewidzia e zwyciy druyna A;
                </Segment>
                <Segment>
                *przykad 2: Gracz postawi wynik A 1:1 B, lecz mecz zakoczy si wynikiem A 0:0 B  gracz otrzymuje 2 punkty, gdy przewidzia e w meczu padnie remis;                
                </Segment>
                <Segment>
                - Jeeli gracz prawidowo wytypuje dokadny wynik meczu, otrzymuje 5 punktw. 2 punkty za prawidowo wytypowanego zwycizc, oraz dodatkowo 3 punkty za trafiony dokadny wynik;                
                </Segment>
                <Segment>
                - Jeeli gracz niepoprawnie wytypuje mecz, otrzymuje 0 punktw za ten mecz;
                </Segment>
            </Segment.Group>
            <Segment.Group>
                <Segment>
                <Header as='h2' content='Faza pucharowa' />
                </Segment>
                <Segment>
                - w fazie pucharowej mecze s traktowane caociowo  wliczane s rwnie dogrywki oraz konkurs rzutw karnych. Gracze nie mog obstawia remisw. Wygrane rzuty karne traktowane s jako +1 gol dla druyny zwyciskiej:
                </Segment>
                <Segment>
                *przykad 1: Mecz po 120 przy wyniku 1:1 wci pozostaje nierozstrzygnity, druyna A wygrywa w konkursie rzutw karnych. Za typ obstawiajcy zwycistwo druyny A gracz otrzymuje 2 punkty, a za obstawienie wyniku A 2:1 B - dodatkowe 3 punkty;
                </Segment>
                <Segment>
                *przykad 2: Mecz po 120 przy wyniku 1:1 wci pozostaje nierozstrzygnity, druyna A wygrywa po dogrywce 3:1. Prawidowo wytypowany wynik to A 3:1 B ( w zakadach bukmacherskich prawidowo wytypowanym wynikiem byoby 1:1)
                </Segment>
            </Segment.Group>
            <Segment.Group>
                <Segment>
                <Header as='h2' content='Punkty BONUSOWE' />
                </Segment>
                <Segment>
                - gracze przed rozpoczciem pierwszego meczu na M typuj kocowego zwycizc oraz krla strzelcw na turnieju. Za kady poprawny traf z dwch bonusowych zakadw gracz otrzymuje po 10 punktw. Gracz moe zdoby bonusowo 0 , 10 lub 20 punktw dodatkowo. 
                </Segment>
            </Segment.Group>
        </Container>
    )
}
